
          @import "@/assets/css/common.scss";
          @import "@/assets/css/fontface.scss";
        

.detail-wrapper {
  .title {
    margin: 64px 32px;
  }

  .tag-list {
    margin-top: 24px;
    padding: 24px 24px;

    border-top: 1px solid $color-primary;
    border-bottom: 1px solid $color-primary;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 12px 16px;
    label {
      background-color: $color-primary;
      color: $color-primary-white;
      padding: 4px 8px;
    }
  }

  .date {
    margin-top: 16px;
  }

  .post {
    margin: 0 auto;
    padding: 64px 32px;
    max-width: 720px;
    text-align: left;
    word-break: keep-all;

    img {
      max-width: 100%;
      outline: 1px solid $color-primary;
    }
    pre {
      background-color: #dadada;
      margin: 24px 0;
      padding: 16px;
      overflow-x: auto;
      line-height: 1.3;
    }
    code {
      background-color: #dadada;
      padding: 2px 3px 2px 4px;
      border-radius: 2px;
    }
    a {
      &:link {
        color: $color-primary;
        font-weight: 700;
      }
      &:visited {
        color: $color-primary;
      }
      &:before {
        content: "ðŸ“Ž ";
      }
    }
  }
}

